import React, { useState } from 'react';
import "./GiveMAGiftModel.scss";
import Button from '../button-component/ButtonComponent';
import Modal from "../modal-component/ModalComponent";
import Counter from "../counter/Counter";
import ProductDetailsModel from '../product-details-model/ProductDetailsModel'
import { connect } from "react-redux";
import AlreadyBoughtProduct from "modules/commons/components/already-bought-product/AlreadyBoughtProduct";

class GiveMAGiftModel extends React.Component {
	// Variable declarations
	state = {
		show: false,
		updateModal: false,
		modalTitle: '',
		quantity: 1,
		isBoughtThisAlready: false,
		name: null,
		email: null,
		storeName: null,
		orderNumber: null,
		giftMessage: null,
		amount: "0.00",
	}

	modalTitleContent = ""

	componentDidMount = () => {
		this.setModalData()
		this.setState(() => ({
			show: true,
		}))
	}

	incrementQuantity = () => {
		this.setState(() => ({
			quantity: this.state.quantity + 1
		}))
	}

	decrementQuantity = () => {
		if (this.state.quantity > 1) {
			this.setState(() => ({
				quantity: this.state.quantity - 1
			}))
		}
	}

	iBoughtThisAlready = () => {
		this.setState(() => ({
			isBoughtThisAlready: true,
			modalTitle: 'I Bought This Already',
		}))
	}

	nameChangeHandler = (ev) => {
		ev.persist();
		this.setState({
			name: ev.target.value
		})
	}

	emailChangeHandler = (ev) => {
		ev.persist();
		this.setState({
			email: ev.target.value
		})
	}

	storeNameChangeHandler = (ev) => {
		ev.persist();
		this.setState({
			storeName: ev.target.value
		})
	}

	orderNumberChangeHandler = (ev) => {
		ev.persist();
		this.setState({
			orderNumber: ev.target.value
		})
	}

	giftMessageChangeHandler = (ev) => {
		ev.persist();
		this.setState({
			giftMessage: ev.target.value
		})
	}

	amountChangeHandler = (ev) => {
		this.setState({
			amount: ev.target.value
		})
	}

	submitDataHandler = () => {
		if (this.state.isBoughtThisAlready) {

		} else {

		}
	}

	setModalData = () => {
		this.clearModalData()
		this.setState({
			modalTitle: this.props.title
		})
	}

	clearModalData = () => {
		this.state.modalTitle = ""
		this.modalBody = ""
		this.modalTitleContent = ""
		this.modalFooterButtons = ""
	}

	modalCloseHandler = () => {
		// this.clearModalData()
		this.setState({
			show: false
		})
	}


	render() {
		// Body for modal
		let modalBody = (
			<>
				{/* Give M+A Gift Model Body */}
				{!this.state.isBoughtThisAlready ?
					<div className="row add-gift-another-store">
						<div className="col">
							<div className="row">
								<>
									<div className="col-5 py-4 add-gift-image-select">
										<div className="mr-3">
											<ProductDetailsModel data={this.props.data} />
										</div>
									</div>
									{!this.props.isGroupGift ?
										<div className="col-7 mt-4">
											<div className="ml-3">
												<div className="row col">
													<div className="col">
														<p className="font-body font-medium">Buy this gift for {this.props.stateRegistryInfo.user.firstName} {this.props.stateRegistryInfo.user.partnerUser ? 'and' + this.props.stateRegistryInfo.user.partnerUser : ''}</p>
														<p className="text-sm mt-1">We’ll keep the gift available in the registry until you checkout of the cart</p>
													</div>
												</div>
												<div className="row col mt-4">
													<div className="col m-a-input-container">
														<p className="text-sm font-medium add-gift-store-label">Quantity</p>
														<Counter
															className="mt-2"
															counter={this.state.quantity}
															incrementHandler={this.incrementQuantity}
															decrementHandler={this.decrementQuantity}
														/>
													</div>
												</div>
											</div>
										</div>
										:
										// For group gift
										<div className="col-7 mt-4">
											<div className="ml-3">
												<div className="row col">
													<div className="col">
														<p className="font-body font-medium">How much do you want to contribute?</p>
														<p className="text-sm mt-1">
															{this.props.stateRegistryInfo.user.firstName} {this.props.stateRegistryInfo.user.partnerUser ? 'and' + this.props.stateRegistryInfo.user.partnerUser : ''} will be able to buy the gift themselves once they receive the funds.
														</p>
													</div>
												</div>
												{this.props.data.isGroupGift ?
													this.props.data.item.hideTotal ? null :
														<div className="row col mt-4 give-gift-model">
															<div className="col">
																<span className="text-sm font-medium received-amount">Received ${this.props.data.groupGift ? this.props.data.groupGift.amountReceived : 0} </span>
																<span className="text-sm total-amount"> of ${this.props.data.item.price}</span>
																<div className="group-gift-background">
																	<div className="group-gift-progress mw-100" style={{ width: `${this.props.data.groupGift ? this.props.data.groupGift.amountReceived / this.props.data.item.price * 100 : 0}%` }} />
																</div>
															</div>
														</div>
													: null}
											</div>
											<div className="mt-4 ml-3">
												<div className="row col">
													<div className="col">
														<div className="m-a-input-container mr-2">
															<p className="text-sm font-medium add-gift-store-label">Amount</p>
															<div className="price-input mt-1">
																<input
																	type="text"
																	className="w-100 pl-2 pr-1 py-1 text-body pl-5"
																	value={this.state.amount}
																	onChange={(ev) => this.amountChangeHandler(ev)}
																/>
															</div>
															{/*<span className="price-input text-body">$</span>*/}
														</div>
														{this.props.data.item.hideTotal ?
															null : <p className="mt-3 text-sm give-gift-text-link font-medium">Contribute the rest</p>}
													</div>
												</div>
											</div>
										</div>

									}
								</>
							</div>
						</div>
					</div>
					: <AlreadyBoughtProduct data={this.props.data} />}
			</>
		)

		// Modal buttons
		let modalFooterButtons = (
			<>
				{!this.state.isBoughtThisAlready ?
					<Button
						variant="secondary"
						className="MA-gift-footer-btn px-3 py-1 font-medium text-body"
						onClick={() => this.iBoughtThisAlready()}
					>I Bought This Already</Button> : null}
				<Button
					variant="primary"
					className="MA-gift-footer-btn px-4 py-1 font-medium text-body"
					onClick={() => this.submitDataHandler()}
				>{!this.state.isBoughtThisAlready ? 'Add To Cart' : 'Save & Close'}</Button>
			</>
		)

		return (
			<>
				<Modal
					show={this.props.show}
					updateModal={this.state.updateModal}
					title={this.state.modalTitle}
					titleContent={this.modalTitleContent}
					footer={modalFooterButtons}
					closeModal={this.props.close}>
					{modalBody}
				</Modal>
			</>
		)
	}
}
const mapStateToProps = state => {
	return {
		stateRegistryInfo: state.guestView.info,
		productBoughtAlreadyInfo: state.guestView.alreadyBoughtProduct,
	}
}

export default connect(
	mapStateToProps
)(GiveMAGiftModel);